export const auth = firebase.auth();
export const firestore = firebase.firestore();

const provider = new firebase.auth.GoogleAuthProvider();

export const signInWithGoogle = () => {
  auth.signInWithPopup(provider);
};

export const generateUserDocument = async (user, additionalData) => {
  if (!user) return;

  const userRef = firestore.doc(`users/${user.uid}`);
  const snapshot = await userRef.get();

  if (!snapshot.exists) {
    const { email, displayName } = user;
    let watchlist = [];
    try {
      await userRef.set({
        displayName,
        email,
        watchlist,
        ...additionalData,
      });
    } catch (error) {
      console.error('Error creating user document', error);
    }
  }
  return getUserDocument(user.uid);
};

const getUserDocument = async (uid) => {
  if (!uid) return null;
  try {
    const userDocument = await firestore.doc(`users/${uid}`).get();

    return {
      uid,
      ...userDocument.data(),
    };
  } catch (error) {
    console.error('Error fetching user', error);
  }
};

export const addToUserWatchList = async (uid, data) => {
  if (!uid) return null;
  try {
    const userRef = firestore.doc(`users/${uid}`);
    const userDocument = await userRef.get();
    const userWatchlist = await userDocument.data().watchlist;
    console.log(userWatchlist);

    //check if the movie already exists in watchlist
    if (userWatchlist.some((e) => e.id === data.id)) {
      //already exists
    } else {
      userWatchlist.push(data);
      userRef.set({ watchlist: userWatchlist }, { merge: true });
    }
  } catch (error) {
    console.error('Error fetching user', error);
  }
};

